  width: 100%;
  background: #fff;
  padding: 30px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -o-border-radius: 10px;
  -ms-border-radius: 10px;
  border-radius: 10px;
  box-shadow: unset;
  text-align: left;
  z-index: 11;
}

.modal a.close-modal {
  background-image: url(../img/sprite.svg#exit);
